$gold:linear-gradient(90deg, #fcff9e 0%, #c67700 100%);
.mint-section{
    width:100vw;
    margin-top: 1.5rem;
    // background-color: blue;
    margin-bottom: 5rem;
}
.mint-section-inner{
    display: flex;
    justify-content: space-between;
    width: 1200px;
    // background-color: red;
    margin: auto;
    max-width: 95vw;
}
.mint-section-left{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 2rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 560px;
    width: 42%;
    background-color: #202020;
    border: 1px solid white;
}
.amount-wrapper,.info-wrapper{
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 85px;
    width: 100%;
    border-bottom: 1px solid white;
}
.amount-wrapper-inner{
    
    display: flex;
    align-items: center;
    height: 80%;
    input{
        display: flex;
        align-items: center;
        text-align: center;
        height: 40px;
        width: 40px;
        border: 2px solid rgb(235, 190, 79);
        border-radius: 5px;
        outline: none;
        background: transparent;
        margin: 0 12px 0 12px;
    }
}
.amount-btn,.max-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height:35px;
    width: 42px;
    border: none;
    background-color: white;
    border-radius: 5px;
    border: 1px solid white;
    line-height: 0px;
    cursor: pointer;
    &:hover{
        background-color: transparent;
        color:white;
    }
    span{
        margin-top: -5px;
    }
}
.max-btn{
    width: 60px;
    margin-left: 11px;
}
.mint-button{
    margin-top: 3rem;
    cursor: pointer;
    width: 100%;
    height: 60px;
    border: none;
    background: $gold;

    &:hover{
        background:white;
        color:black;
    }
}
@media screen and(max-width:975px) {
    .mint-section-inner{
        flex-direction: column-reverse;
        align-items: center;
    }
    .mint-section-left{
        width: 100%;
        margin-top: 2rem;
    }
}
@media screen and(max-width:405px) {
 
    .mint-section-left{
        padding: 1rem;
    }
}